<template>
  <div class="Japanese">
    <h3 class="nav_btn" v-on:click="changeShowType()">平假名</h3>
    <div class="nav_bg">
      <td class="nav" v-for="(item,key) in nav_title" v-bind:key="item.id">
        <h2 :class="{'nav_item': true,
        'nav_active': key === nav_chose, 'left_radius': key===0, 'right_radius': key === nav_title.length-1}"
            v-on:click = "onNavSelected(key)" >{{item.name}}</h2>
      </td>
    </div>

    <table class="container">
      <tr v-for="v in current" class="row">
        <td v-for="item in v" :class="{'normal_row': nav_chose!==2,'less_row':nav_chose===2}" v-on:click = "onItemSelected(item, $event)">
          <div >
            <h4 :class="{'letter': true, 'active': item.name === selected }"> {{showType === 0? item.left:item.right}}</h4>
            <h4 id="name" > {{item.name}}</h4>
          </div>
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'

  export default {
    name: 'Japanese',
    computed: mapGetters({
      language: 'getCurLan'
    }),
    created(){
      this.gamemuiscs1 = new Audio()
      this.current = this.V_less
      switch (this.language){
        case 'Chinese':
          this.nav_title = [{name:'轻音'}, {name:'浊音'},{name:'拗音'}];
          break
        case 'English':
          this.nav_title = [{name:'V_less'}, {name:'Voiced'},{name:'Bend'}];
          break
      }
    },

    methods: {
      onItemSelected (item, event) {
        this.selected = item.name
//        event.stopPropagation();
        if(item.raw !== ''){
          this.gamemuiscs1 = new Audio("./static/rawes/"+item.raw);
          this.gamemuiscs1.play();
        }
      },
      onNavSelected (key) {
        this.nav_chose = key
        switch (key){
          case 0:
            this.current = this.V_less
            break
          case 1:
            this.current = this.Voiced
            break
          case 2:
            this.current = this.Bend
            break
        }
      },
      changeShowType(){
        this.showType ^= 1;
      }
    },

    data () {
      return {
        nav_chose: 0,
        selected:'',
        showType: 0,  //0 平假名，1 片假名
        nav_title: [{name:'V_less'}, {name:'Voiced'},{name:'Bend'}],
        current: [
        ],
        //清音
        V_less: [
          [
            {name: 'a', left: 'あ', right: 'ア', raw: 'a.wav'} ,{name: 'i', left: 'い', right: 'イ', raw: 'i.wav'},
            {name: 'u', left: 'う', right: 'ウ', raw: 'u.wav'},{name: 'e', left: 'え', right: 'エ', raw: 'e.wav'},
            {name: 'o', left: 'お', right: 'オ', raw: 'o.wav'}
          ],

          [
            {name: 'ka', left: 'か', right: 'カ', raw: 'ka.wav'}, {name: 'ki', left: 'き', right: 'キ', raw: 'ki.wav'},
            {name: 'ku', left: 'く', right: 'ク', raw: 'ku.wav'}, {name: 'ke', left: 'け', right: 'ケ', raw: 'ke.wav'},
            {name: 'ko', left: 'こ', right: 'コ', raw: 'ko.wav'}
          ],

          [
            {name: 'sa', left: 'さ', right: 'サ', raw: 'sa.wav'},{name: 'shi', left: 'し', right: 'シ', raw: 'shi.wav'},
            {name: 'su', left: 'す', right: 'ス', raw: 'su.wav'},{name: 'se', left: 'せ', right: 'セ', raw: 'se.wav'},
            {name: 'so', left: 'そ', right: 'ソ', raw: 'so.wav'}
          ],
          [
            {name: 'ta', left: 'た', right: 'タ', raw: 'ta.wav'}, {name: 'chi', left: 'ち', right: 'チ', raw: 'chi.wav'},
            {name: 'tsu', left: 'つ', right: 'ツ', raw: 'tsu.wav'}, {name: 'te', left: 'て', right: 'テ', raw: 'te.wav'},
            {name: 'to', left: 'と', right: 'ト', raw: 'to.wav'}
          ],
          [
            {name: 'na', left: 'な', right: 'ナ', raw: 'na.wav'},{name: 'ni', left: 'に', right: '二', raw: 'ni.wav'},
            {name: 'nu', left: 'ぬ', right: 'ヌ', raw: 'nu.wav'},{name: 'ne', left: 'ね', right: 'ネ', raw: 'ne.wav'},
            {name: 'no', left: 'の', right: 'ノ', raw: 'no.wav'}
          ],
          [
            {name: 'ha', left: 'は', right: 'ハ', raw: 'ha.wav'}, {name: 'hi', left: 'ひ', right: 'ヒ', raw: 'hi.wav'},
            {name: 'fu', left: 'ふ', right: 'フ', raw: 'fu.wav'}, {name: 'he', left: 'へ', right: 'へ', raw: 'he.wav'},
            {name: 'ho', left: 'ほ', right: 'ホ', raw: 'ho.wav'}
          ],
          [
            {name: 'ma', left: 'ま', right: 'マ', raw: 'ma.wav'},{name: 'mi', left: 'み', right: 'ミ', raw: 'mi.wav'},
            {name: 'mu', left: 'む', right: 'ム', raw: 'mu.wav'},{name: 'me', left: 'め', right: 'メ', raw: 'me.wav'},
            {name: 'mo', left: 'も', right: 'モ', raw: 'mo.wav'}
          ],
          [
            {name: 'ya', left: 'や', right: 'ヤ', raw: 'ya.wav'}, {name: '.', left: '.', right: '.', raw: ''},
            {name: 'yu', left: 'ゆ', right: 'ユ', raw: 'yu.wav'}, {name: '.', left: '.', right: '.', raw: ''},
            {name: 'yo', left: 'よ', right: 'ヨ', raw: 'yo.wav'}
          ],

          [
            {name: 'ra', left: 'ら', right: 'ラ', raw: 'ra.wav'}, {name: 'ri', left: 'り', right: 'リ', raw: 'ri.wav'},
            {name: 'ru', left: 'る', right: 'ル', raw: 'ru.wav'}, {name: 're', left: 'れ', right: 'レ', raw: 're.wav'},
            {name: 'ro', left: 'ろ', right: 'ロ', raw: 'ro.wav'},
          ],
          [
            {name: 'wa', left: 'わ', right: 'ワ', raw: 'wa.wav'}, {name: '.', left: '.', right: '.', raw: ''},
            {name: 'wo', left: 'を', right: 'ヲ', raw: 'wo.wav'}, {name: '.', left: '.', right: '.', raw: ''},
            {name: 'n', left: 'ん', right: 'ン', raw: 'n.wav'},
          ]
        ],
        //浊音
        Voiced: [
          [
            {name: 'ga', left: 'が', right: 'ガ', raw: 'ga.wav'},{name: 'gi', left: 'ぎ', right: 'ギ', raw: 'gi.wav'},
            {name: 'gu', left: 'ぐ', right: 'グ', raw: 'gu.wav'},{name: 'ge', left: 'げ', right: 'ゲ', raw: 'ge.wav'},
            {name: 'go', left: 'ご', right: 'ゴ', raw: 'go.wav'}
          ],
          [
            {name: 'za', left: 'ざ', right: 'ザ', raw: 'za.wav'},{name: 'zi', left: 'じ', right: 'ジ', raw: 'ji.wav'},
            {name: 'zu', left: 'ず', right: 'ズ', raw: 'zu.wav'},{name: 'ze', left: 'ぜ', right: 'ゼ', raw: 'ze.wav'},
            {name: 'zo', left: 'ぞ', right: 'ゾ', raw: 'zo.wav'}
          ],
          [
            {name: 'da', left: 'だ', right: 'ダ', raw: 'da.wav'},{name: 'di', left: 'ぢ', right: 'ヂ', raw: 'ji.wav'},
            {name: 'du', left: 'づ', right: 'ヅ', raw: 'zu.wav'},{name: 'de', left: 'で', right: 'デ', raw: 'de.wav'},
            {name: 'do', left: 'ど', right: 'ド', raw: 'do1.wav'}
          ],
          [
            {name: 'ba', left: 'ば', right: 'バ', raw: 'ba.wav'},{name: 'bi', left: 'び', right: 'ビ', raw: 'bi.wav'},
            {name: 'bu', left: 'ぶ', right: 'ブ', raw: 'bu.wav'},{name: 'be', left: 'べ', right: 'ベ', raw: 'be.wav'},
            {name: 'bo', left: 'ぼ', right: 'ボ', raw: 'bo.wav'}
          ],
          [
            {name: 'pa', left: 'ぱ', right: 'パ', raw: 'pa.wav'},{name: 'pi', left: 'ぴ', right: 'ピ', raw: 'pi.wav'},
            {name: 'pu', left: 'ぷ', right: 'プ', raw: 'pu.wav'},{name: 'pe', left: 'ぺ', right: 'ペ', raw: 'pe.wav'},
            {name: 'po', left: 'ぽ', right: 'ポ', raw: 'po.wav'}
          ]

        ],
        //拗音
        Bend: [
          [
            {name: 'kya', left: 'きゃ', right: 'キャ', raw: 'kya.wav'}
            ,{name: 'kyu', left: 'きゅ', right: 'キュ', raw: 'kyu.wav'}
            ,{name: 'kyo', left: 'きょ', right: 'キョ', raw: 'kyo.wav'}
          ],
          [
            {name: 'gya', left: 'ぎゃ', right: 'ギャ', raw: 'gya.wav'}
            ,{name: 'gyu', left: 'ぎゅ', right: 'ギュ', raw: 'gyu.wav'},
            {name: 'gyo', left: 'ぎょ', right: 'ギョ', raw: 'gyo.wav'}
          ],
          [
            {name: 'sha', left: 'しゃ', right: 'シャ', raw: 'sha.wav'}
            ,{name: 'shu', left: 'しゅ', right: 'シュ', raw: 'shu.wav'},
            {name: 'sho', left: 'しょ', right: 'ショ', raw: 'sho.wav'}
          ],
          [
            {name: 'jya', left: 'じゃ', right: 'ジャ', raw: 'jya.wav'}
            ,{name: 'jyu', left: 'じゅ', right: 'ジュ', raw: 'jyu.wav'},
            {name: 'jyo', left: 'じょ', right: 'ジョ', raw: 'jyo.wav'}
          ],

          [
            {name: 'cya', left: 'ちゃ', right: 'チャ', raw: 'cha.wav'}
            ,{name: 'cyu', left: 'ちゅ', right: 'チュ', raw: 'chu.wav'},
            {name: 'cyo', left: 'ちょ', right: 'チョ', raw: 'cho.wav'}
          ],

          [
            {name: 'hya', left: 'ひゃ', right: 'ヒャ', raw: 'hya.wav'}
            ,{name: 'hyu', left: 'ひゅ', right: 'ヒュ', raw: 'hyu.wav'},
            {name: 'hyo', left: 'ひょ', right: 'ヒョ', raw: 'hyo.wav'}
          ],

          [
            {name: 'nya', left: 'にゃ', right: 'ニャ', raw: 'nya.wav'}
            ,{name: 'nyu', left: 'にゅ', right: 'ニュ', raw: 'nyu.wav'},
            {name: 'nyo', left: 'にょ', right: 'ニョ', raw: 'nyo.wav'}
          ],

          //重复的
//          [
//            {name: 'hya', left: 'ひゃ', right: 'ヒャ', raw: 'hya.wav'}
//            ,{name: 'hyu', left: 'ひゅ', right: 'ヒュ', raw: 'hyu.wav'},
//            {name: 'hyo', left: 'ひょ', right: 'ヒョ', raw: 'hyo.wav'}
//          ],

          [
            {name: 'bya', left: 'びゃ', right: 'ビャ', raw: 'bya.wav'}
            ,{name: 'byu', left: 'びゅ', right: 'ビュ', raw: 'byu.wav'},
            {name: 'byo', left: 'びょ', right: 'ビョ', raw: 'byo.wav'}
          ],
          [
            {name: 'pya', left: 'ぴゃ', right: 'ピャ', raw: 'pya.wav'}
            ,{name: 'pyu', left: 'ぴゅ', right: 'ピュ', raw: 'pyu.wav'},
            {name: 'pyo', left: 'ぴょ', right: 'ピョ', raw: 'pyo.wav'}
          ],
          [
            {name: 'mya', left: 'みゃ', right: 'ミャ', raw: 'mya.wav'}
            ,{name: 'myu', left: 'みゅ', right: 'ミュ', raw: 'myu.wav'},
            {name: 'myo', left: 'みょ', right: 'ミョ', raw: 'myo.wav'}
          ],
          [
            {name: 'rya', left: 'りゃ', right: 'リャ', raw: 'rya.wav'}
            ,{name: 'ryu', left: 'りゅ', right: 'リュ', raw: 'ryu.wav'},
            {name: 'ryo', left: 'りょ', right: 'リョ', raw: 'ryo.wav'}
          ],


        ]
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style >

  .nav_bg {
    width:96%;
    text-align: center;
    padding-left: 3%;
    padding-right: 1%;
    /*border-radius: 0em 0em 1em 1em;*/
    box-shadow: 0.1em 0.3em 0.3em #072927;
    margin-bottom: 0.3em;
    background: #f0ba02;
    /*background: linear-gradient(#f0ba02, #cd7015, #f0ba02);*/
  }

  .nav{
    width: 4em;
  }

  .nav_btn{
    float: right;
    margin-right: 8%;
    margin-top: 0.5em;
    font-size: 1.2em;
    color: white;
    font-weight: bold;
    background: transparent;
  }
  .nav_item{
    text-align: center;
    margin-top: 0.5em;
    padding: 5px;
    font-size: 1em;
    background: #cd7015;
    border-color: brown;
  }
  .left_radius{
    border-radius: 1em 0em 0em 1em;
  }

  .right_radius{
    border-radius: 0em 1em 1em 0em;
  }

  .nav_active{
    background: white;
    border-color: #cd7015;
    color: coral;
    font-weight:bold;
  }

  .Japanese{
    padding-bottom: 1%;
  }
  .container{
    width:100%;
    text-align: center;
    margin-bottom: 5%;
    /*border-spacing: inherit;*/
    background: gray;
    empty-cells: show;
  }
  .container tr {
    text-align: center;
    /*background-color: #21ff13;*/
    display: flex;
  }

  .normal_row{
    text-align: center;
    width:20%;
  }

  .less_row{
    width:33%;
    text-align: center;
  }


  .letter{
    margin: 0%;
    font-size: 1.4em;
    line-height: 1.5em;
    color: #787250;
    background: #F8F6EA;
    padding-top: 0.4em;
    padding-bottom: 0.2em;
  }

  .active{
    font-size: 2.0em;
    line-height: 0.87em;
    color: coral;
    font-weight:bold;
  }

  #name{
    margin: 0%;
    font-size: 1.4em;
    line-height: 1.5em;
    color: #928B64;
    background: #EAE7D1;
    padding-bottom: 1%;
    font-size: 1em;
  }

  .box {
    position: absolute;
    width: 80px;
    height: 50px;
    background-color: #09f;
    /*display: none;*/
  }

</style>



