const state = {
  _current_lan: 'English',
  _languages: ['English', 'Chinese']
}

const getters = {
    getCurLan (state) {
      return state._current_lan
    },
    getSuportLans(state){
      return state._languages
    }
}

const mutations = {
  setCurrentLan:(state, lan)=>{state._current_lan = lan},
}

const actions = {
  changeLan (context, lan) {
    context.commit('setCurrentLan', lan)
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



