import Vue from 'vue'
import Router from 'vue-router'
import Japanese from '@/components/Japanese'
import HelloWorld from '@/components/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/japanese',
      name: 'Japanese',
      component: Japanese
    }
  ]
})



