<template>
  <div class="hello">
    <!--<pan class="lan_selector">-->
      <select v-model="selected" @change="onLanguageSelected">
        <option>{{selected}}</option>
        <option v-for="item in languages" v-bind:value="item">{{item}}</option>
      </select>
    <!--</pan>-->
    <img src="https://nebulas.io/assets/images/nebulasx60.png">
    <h1>{{welcome}}</h1>
    <p v-on:click="onCourseSelected('Japanese')">{{japanese_learning}}</p>
    <p v-on:click="onCourseSelected('Korean')">{{korean_learning}}</p>
    <p v-on:click="onCourseSelected('English')">{{English_learning}}</p>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'

  export default {
    name: 'HelloWorld',
    computed: mapGetters({
      languages: 'getSuportLans',
      currentLan: 'getCurLan'
    }),

    created() {
      this.selected = this.currentLan
      this.refleshShowLan();
    },

    data() {
      return {
        selected: '',
        welcome: 'Welcome to Nebulats language letters!',
        japanese_learning: 'Start Japanese Learning',
        korean_learning: 'Start Korean Learning',
        English_learning: 'Start English Learning'
      }
    },
    methods: {
      onCourseSelected(lang) {
        if (lang !== 'Japanese') {
          alert("敬请期待")
//          this.$store.dispatch("startPay", 0.0001)
        } else {
          this.$router.push(lang)
        }
      },
      onLanguageSelected() {
        this.$store.dispatch("changeLan", this.selected)
        this.refleshShowLan()
      },
      refleshShowLan() {
        switch (this.selected) {
          case "English":
            this.welcome = 'Welcome to Nebulats language letters!'
            this.japanese_learning = 'Start Japanese Learning >>'
            this.korean_learning = 'Start Korean Learning >>'
            break
          case "Chinese":
            this.welcome = '欢迎使用Nebulats字母发音表!'
            this.japanese_learning = '开始日语学习 >>'
            this.korean_learning = '开始韩语学习 >>'
            this.English_learning = '开始英语学习'
            break
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

  select {
    width: 5em;
    font-size: 2em;
    float: right;
    margin-right: 10%;
    background: cyan;
  }

  div {
    margin-top: 2em;
    padding-bottom: 2em;
  }

  h1 {
    font-style: italic;
    margin-bottom: 2em;
    color: rgba(6, 39, 43, 0.87);
  }

  p {
    font-style: italic;
    font-size: 1.2em;
    font-family: "Microsoft Sans Serif";
    font-weight: bold;
  }

  p:hover {
    color: #cd7015;;
  }
</style>



